
.gallery-container {
    width: 100%;
    font-family: 'Microsoft YaHei', 'PingFang SC', sans-serif;
    background: linear-gradient(180deg, rgba(15, 20, 35, 0.95) 0%, rgba(10, 15, 30, 0.98) 100%);
    border-radius: 12px;
    overflow: hidden;
    color: #e0e8f0;
}

// Ê†áÈ¢òÊ†è
.gallery-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 16px 20px;
    background: linear-gradient(90deg, rgba(60, 80, 120, 0.4) 0%, rgba(100, 60, 120, 0.4) 100%);
    border-bottom: 1px solid rgba(100, 150, 200, 0.2);

    .header-title {
        display: flex;
        align-items: center;
        gap: 10px;
        font-size: 18px;
        font-weight: bold;
        color: #e8f0ff;

        i {
            color: #88aaff;
        }
    }

    .header-info {
        display: flex;
        align-items: center;
        gap: 16px;
        font-size: 13px;
        color: #8090a0;

        .current-char {
            display: flex;
            align-items: center;
            gap: 6px;
            padding: 4px 10px;
            background: rgba(100, 150, 200, 0.2);
            border-radius: 12px;

            i {
                color: #88aacc;
            }
        }
    }
}

// ËßíËâ≤Á≠õÈÄâ
.character-filter {
    display: flex;
    flex-wrap: wrap;
    gap: 8px;
    padding: 12px 20px;
    background: rgba(25, 30, 50, 0.5);
    border-bottom: 1px solid rgba(100, 150, 200, 0.1);

    .filter-btn {
        padding: 6px 14px;
        background: rgba(50, 60, 80, 0.5);
        border: 1px solid rgba(100, 150, 200, 0.2);
        border-radius: 16px;
        color: #8090a0;
        font-size: 12px;
        cursor: pointer;
        transition: all 0.2s;

        &:hover {
            background: rgba(80, 100, 140, 0.4);
            color: #b0c0d0;
        }

        &.active {
            background: linear-gradient(135deg, rgba(80, 120, 180, 0.5) 0%, rgba(120, 80, 160, 0.5) 100%);
            border-color: rgba(100, 150, 200, 0.5);
            color: #e0f0ff;
        }
    }
}

// ÂõæÁâáÁΩëÊ†º - Ê∞¥Âπ≥ÊªöÂä®Â∏ÉÂ±Ä
.gallery-grid {
    display: flex;
    flex-wrap: nowrap;
    justify-content: center; // ÂΩìÂç°ÁâáÂ∞ëÊó∂Â±Ö‰∏≠ÊòæÁ§∫
    gap: 20px;
    padding: 20px;
    min-height: 200px;
    overflow-x: auto;
    overflow-y: hidden;
    scroll-behavior: smooth;
    -webkit-overflow-scrolling: touch;

    // Ëá™ÂÆö‰πâÊªöÂä®Êù°Ê†∑Âºè
    &::-webkit-scrollbar {
        height: 8px;
    }

    &::-webkit-scrollbar-track {
        background: rgba(30, 40, 60, 0.5);
        border-radius: 4px;
    }

    &::-webkit-scrollbar-thumb {
        background: rgba(100, 150, 200, 0.4);
        border-radius: 4px;

        &:hover {
            background: rgba(100, 150, 200, 0.6);
        }
    }
}

// ÁøªËΩ¨Âç°Áâá
// ============================================================================
// üé® Âç°ÁâáÂ∞∫ÂØ∏ÈÖçÁΩÆ - ‰øÆÊîπËøôÈáåÁöÑÂÄºÂèØ‰ª•Ë∞ÉÊï¥Âç°ÁâáÂ§ßÂ∞è
// ============================================================================
.flip-card {
    flex-shrink: 0; // Èò≤Ê≠¢Âç°ÁâáË¢´ÂéãÁº©
    width: 320px; // Âõ∫ÂÆöÂç°ÁâáÂÆΩÂ∫¶
    aspect-ratio: 2 / 3; // ‰ΩøÁî®ÂÆΩÈ´òÊØî‰ª£ÊõøÂõ∫ÂÆöÈ´òÂ∫¶Ôºå‰øùÊåÅ 2:3 ÊØî‰æã
    perspective: 1000px;
    cursor: pointer;

    &:hover .flip-card-inner {
        box-shadow: 0 15px 40px rgba(100, 150, 255, 0.4);
        transform: scale(1.03);
    }
}

.flip-card-inner {
    position: relative;
    width: 100%;
    height: 100%;
    transition: transform 0.7s cubic-bezier(0.4, 0, 0.2, 1), box-shadow 0.3s;
    transform-style: preserve-3d;
    border-radius: 14px;
    box-shadow: 0 8px 25px rgba(0, 0, 0, 0.5);
}

.flip-card.flipped .flip-card-inner {
    transform: rotateY(180deg);
}

.flip-card-front,
.flip-card-back {
    position: absolute;
    width: 100%;
    height: 100%;
    backface-visibility: hidden;
    border-radius: 14px;
    overflow: hidden;
    border: 2px solid rgba(100, 150, 200, 0.3);

    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        transition: transform 0.3s;
    }

    &:hover img {
        transform: scale(1.05);
    }
}

.flip-card-front {
    .card-overlay {
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        padding: 16px;
        background: linear-gradient(to top, rgba(0, 0, 0, 0.9) 0%, rgba(0, 0, 0, 0.5) 50%, transparent 100%);
    }

    .card-name {
        font-size: 16px;
        font-weight: bold;
        color: #fff;
        text-shadow: 0 2px 4px rgba(0, 0, 0, 0.8);
        margin-bottom: 4px;
    }

    .card-character {
        font-size: 12px;
        color: rgba(255, 255, 255, 0.7);
    }
}

.flip-card-back {
    transform: rotateY(180deg);
}

// Á©∫Áä∂ÊÄÅ
.empty-state {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 60px 20px;
    color: #506070;

    i {
        font-size: 48px;
        margin-bottom: 16px;
        opacity: 0.5;
    }

    p {
        margin: 4px 0;
        font-size: 14px;
    }

    .hint {
        font-size: 12px;
        color: #405060;
    }
}

// Â∫ïÈÉ®ÊèêÁ§∫
.gallery-footer {
    text-align: center;
    padding: 12px 20px;
    font-size: 12px;
    color: #506070;
    background: rgba(15, 20, 35, 0.5);
    border-top: 1px solid rgba(100, 150, 200, 0.1);
}
